<template>
	<transition name="fade">
		<div class="previewBox" v-show="visable">
			<input id="escClose" @keyup.esc="closeImg" @keyup.37="prevImg" @keyup.39="nextImg" style="position: relative;left: -5000%;top: -5000%;"/>
			<div class="previewImg-mask" @click="closeImg()"></div>
			<div class="previewImg-container">
				<div class="preview-close" @click="closeImg()"></div>
				<div class="arrow">
			    <span class="prev" @click="prevImg">&lt;</span>
			    <span class="next" @click="nextImg">&gt;</span>
		    </div>
				<div class="preview-img">
					<img :src="imgSrc" />
				</div>
				<div class="preview-text">
					<span>{{imgText}}</span>
				</div>
			</div>
		</div>
	</transition>
</template>

<script>
export default {
  name: 'preview',
  props: {
    visable: {
      type: Boolean,
      default: false
    },
 		imgList: {
      type: Object,
      default: {}
    },
  },
  data() {
    return {
    	imgSrc: "",
    	imgText: ""
    }
  },
  methods: {
  	closeImg(){
  		this.$emit("close");
	  	document.body.style.position = "";
	  },
	  prevImg(){
	  	this.imgList.mask -= 1;
	  	if(this.imgList.mask < 0){
	  		this.imgList.mask = this.imgList.list.length -1;
	  	}
	  	this.changeImg(this.imgList.mask);
	  },
	  nextImg(){
	  	this.imgList.mask += 1;
	  	if(this.imgList.mask >= this.imgList.list.length){
	  		this.imgList.mask = 0;
	  	}
	  	this.changeImg(this.imgList.mask);
	  },
	  changeImg(index){
	  	this.imgSrc = this.imgList.list[index].imgSrc;
	  	this.imgText = this.imgList.list[index].imgDesc;
	  	this.$nextTick(() => {
	  		document.getElementById("escClose").focus();
	  		document.body.style.position = "fixed";
	  	})
	  }
  }
}
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
 	.previewBox {
		position: fixed;
    z-index: 2000;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
		.previewImg-mask {
	    position: absolute;
	    z-index: 1;
	    background-color: rgba(0,0,0,.5);
	    top: 0;
	    right: 0;
	    bottom: 0;
	    left: 0;
	    -webkit-transition: opacity .3s;
	    transition: opacity .3s;
	  }
	  .previewImg-container {
	  	.preview-close {
  			position: absolute;
  			top: 0;
  			right: 0;
  			width: 45px;
  			height: 45px;
  			cursor: pointer;
  			z-index: 3;
				background: url(../../assets/images/close.png) no-repeat center center;
				background-size: 100% 100%;
	  	}
			.preview-img {
				display: flex;
	    	text-align: center;
				align-items: center;
	    	justify-content: center;
				height: 80vh;
				margin: 5vh 0;
				img {
					position: relative;
	  			z-index: 2;
					max-width: 100%;
				  max-height: 100%;
				}
			}
			.preview-text {
				display: flex;
	    	text-align: center;
	    	justify-content: center;
				height: 8vh;
				padding: 0 10px;
				margin-bottom: 2vh;
				span {
					position: relative;
	  			z-index: 2;
					font-size: 20px;
					margin: 0;
					color: #fff;
				}
			}
			.arrow {
				span {
					position: absolute;
	  			z-index: 3;
					width: 50px;
					height: 50px;
					line-height: 50px;
					text-align: center;
					background-color: rgba(0,0,0,.1);
					font-size: 20px;
					color: #ffffff;
					top: 50%;
					margin-top: -25px;
					cursor: pointer;
					font-family: "宋体";
					&.prev {
						left: 0;
					}
					&.next {
						right: 0;
					}
					&:hover {
						background-color: rgba(0,0,0,.5);
					}
				}
			}
		}
	}
</style>



