<template>
  <el-container v-loading="loading"
  	 element-loading-text="加载中"
    element-loading-spinner="el-icon-loading"
    element-loading-background="rgba(0, 0, 0, 0.8)">
	  <el-header>
	  	<div class="logo">
	  		<p>个人展示</p>
	  	</div>
	  	<el-menu default-active="profile" class="navBar" mode="horizontal" @select="handleSelect">
			  <el-menu-item index="profile">profile</el-menu-item>
			  <el-menu-item index="picture">picture</el-menu-item>
			  <el-menu-item index="project">project</el-menu-item>
			  <el-menu-item index="remarks">remarks</el-menu-item>
			</el-menu>
	  </el-header>
	  <el-main>
	  	<section class="box" id="profile">
  			<div class="profile-img">
  				<img v-lazy="headImg" />
  			</div>
  			<div class="profile-content">
  				<p>一名前端工作者</p>
  			</div>
  			<div class="skill-table">
  				<el-row :gutter="20">
					  <el-col :xs="24" :sm="12">
		  				<table cellspacing="0">
		  					<thead>
		  						<tr>
		  							<th colspan="2">技能</th>
		  						</tr>
		  					</thead>
		  					<tbody>
		  						<tr>
		  							<td>css+html</td>
		  							<td>css3+html5</td>
		  						</tr>
		  						<tr>
		  							<td>javascript</td>
		  							<td>jquery</td>
		  						</tr>
		  						<tr>
		  							<td>自定义图标iconfont</td>
		  							<td>swiper</td>
		  						<tr>
		  							<td>百度echarts</td>
		  							<td>SCSS</td>
		  						</tr>
		  						<tr>
		  							<td colspan="2">vue + vue-router + axios + vuex</td>
		  						</tr>
		  					</tbody>
		  				</table>
		  			</el-col>
					  <el-col :xs="24" :sm="12">
		  				<table cellspacing="0">
		  					<thead>
		  						<tr>
		  							<th colspan="2">常用ui框架</th>
		  						</tr>
		  					</thead>
		  					<tbody>
		  						<tr>
		  							<td>mui</td>
		  							<td>熟练</td>
		  						</tr>
		  						<tr>
		  							<td>bootstrap</td>
		  							<td>熟练</td>
		  						</tr>
		  						<tr>
		  							<td>element-ui(+vue)</td>
		  							<td>掌握</td>
		  						</tr>
		  						<tr>
		  							<td>layui</td>
		  							<td>掌握</td>
		  						</tr>
		  					</tbody>
		  				</table>
		  			</el-col>
	  			</el-row>
  			</div>
	  	</section>
	  	<section class="bg bg-s"></section>
	  	<section class="box" id="picture">
	  		<el-scrollbar>
					<div class="scroll-box">
	  				<el-tabs tab-position="left">
			    		<el-tab-pane label="muiapp1">
					    	<el-row :gutter="20">
								  <el-col :xs="12" :sm="8" :md="6" :lg="4" v-for="(item,index) in picList1" :key="index" style="margin-bottom: 10px;">
								  	<el-card>
								  		<div class="box-card">
								  			<div class="box-image">
									  			<img v-lazy="item.imgSrc" class="image" @click="previewImg(picList1,index)">
								  			</div>
			    							<div class="box-desc">
			        						<span>{{item.imgDesc}}</span>
			        					</div>
		        					</div>
										</el-card>
							  	</el-col>
							  </el-row>
		    			</el-tab-pane>
					    <el-tab-pane label="muiapp2">
					    	<el-row :gutter="20">
								   <el-col :xs="12" :sm="8" :md="6" :lg="4" v-for="(item,index) in picList2" :key="index" style="margin-bottom: 10px;">
								  	<el-card>
								  		<div class="box-card">
								  			<div class="box-image">
									  			<img v-lazy="item.imgSrc" class="image" @click="previewImg(picList2,index)">
			    							</div>
			    							<div class="box-desc">
			        						<span>{{item.imgDesc}}</span>
			        					</div>
		        					</div>
										</el-card>
							  	</el-col>
							  </el-row>
					    </el-tab-pane>
					    <el-tab-pane label="pcdata">
					    	<el-row :gutter="20">
								   <el-col :xs="12" :sm="8" :md="6" :lg="4" v-for="(item,index) in picList3" :key="index" style="margin-bottom: 10px;">
								  	<el-card>
								  		<div class="box-card">
								  			<div class="box-image">
									  			<img v-lazy="item.imgSrc" class="image" @click="previewImg(picList3,index)">
			    							</div>
			    							<div class="box-desc">
			        						<span>{{item.imgDesc}}</span>
			        					</div>
		        					</div>
										</el-card>
							  	</el-col>
							  </el-row>
					    </el-tab-pane>
					    </el-tab-pane>
			  		</el-tabs>
			   	</div>
				</el-scrollbar>
	  	</section>
	  	<section class="bg bg-b"></section>
	  	<section class="box" id="project">
	  		<el-carousel :interval="4000" type="card" height="320px">
			    <el-carousel-item v-for="(image,index) in projectList" :key="index">
  					<img v-lazy="image.imgSrc"  @click="openIframe(image.imgTitle,image.imgUrl)" />
			    </el-carousel-item>
			  </el-carousel>
	  	</section>
	  	<section class="box" id="remarks">
	  		<p>email：1026689437@qq.com</p>
	  	</section>
	  </el-main>
		<back-to-top transitionName="fade" :customStyle="myBackToTopStyle" :visibilityHeight="100" :backPosition="0"></back-to-top>
	  <el-footer>
	  	<p>本页面由 vue + axios + element-ui + scss 构成</p>
	  </el-footer>
	  <el-dialog :title="iframeTitle" :visible.sync="dialogVisible">
  		<iframe :src="iframeUrl"></iframe>
		  <div slot="footer" class="dialog-footer">
		  	<span>访问地址：</span>
		  	<a target="_blank" :href="projectUrl">{{projectUrl}}</a>
		  </div>
		</el-dialog>
		<!--预览图片弹窗-->
		<preview-img ref="preview" :visable="previewVisible" :imgList="previewList" @close="closePreview"/>
	</el-container>
</template>

<script>
import BackToTop from '@/components/BackToTop'
import PreviewImg from '@/components/PreviewImg'
export default {
  name: 'home',
	components: { BackToTop,PreviewImg },
  data () {
    return {
    	loading: false,
    	scrollPosition: 0,
    	myBackToTopStyle: {
    		zIndex: '1',
        right: '30px',
        bottom: '30px',
        width: '40px',
        height: '40px',
        'border-radius': '4px',
        'line-height': '45px', // 请保持与高度一致以垂直居中 Please keep consistent with height to center vertically
        background: '#e7eaf1'// 按钮的背景颜色 The background color of the button
	    },
	    headImg: 'static/images/head.jpg',
	    iframeUrl: '',
	    projectUrl: '',
	    iframeTitle: 'title',
	    picList1:[],
	    picList2:[],
	    picList3:[],
	    projectList:[],
	    dialogVisible: false,
	    previewVisible: false,
	    previewSrc: "",
	    previewText: "",
	    previewList: {
	    	list:[],
	    	mask: 0,
	    },
	    timer: null
    }
  },
  created: function(){
  	this.loading = true;
  },
  methods: {
	  handleSelect(key, keyPath) {
	  	if(document.getElementById(key)){
	  		this.scrollPosition = document.getElementById(key).offsetTop-60;
      	window.scrollTo(0, this.scrollPosition)
	  	}
	  },
	  openIframe(imgTitle,imgUrl) {
	  	if(imgTitle){
	  		this.iframeTitle = imgTitle;
	  	}
	  	if(imgUrl){
	    	this.dialogVisible = true;
	  		this.iframeUrl = imgUrl;
	  		this.projectUrl = "http://" + location.host + "/" + imgUrl;
	  	}else{
	  		this.$message.error('暂无项目链接！');
	  	}
	  },
	  previewImg(pList,pIndex){
	  	this.previewVisible = true;
	  	this.previewList.list = pList;
	  	this.previewList.mask = pIndex;
			this.$refs.preview.changeImg(pIndex);
	  },
	  closePreview(){
	  	this.previewVisible = false;
	  }
	},
	mounted: function(){
		var _this = this;
		_this.loading = false;
			this.$axios.get('../static/pic-data.json')
			.then(function (response) {
				_this.picList1 = response.data.picList1;
				_this.picList2 = response.data.picList2;
				_this.picList3 = response.data.picList3;
				_this.projectList = response.data.projectList;
			})
			.catch(function (error) {
		    console.log(error);
		  });
	}
}
</script>
<style rel="stylesheet/scss" lang="scss" scoped>
	@media only screen and (max-width: 456px){
		.el-col-xs-12 {
		    width: 100%;
		}
	}
	.el-header {
		background: #fff;
		border-bottom: 1px solid #e6e6e6;
		z-index: 11;
    left: 0;
    right: 0;
    height: 61px !important;
		.navBar {
			float: right;
			border-bottom: none;
		}
		.logo {
			float: left;
			width: 240px;
		}
		.el-menu--horizontal {
			&.navBar {
				text-align: right;
				.el-menu-item {
					float: none;
					display: inline-block;
				}
			}
		}
		.logo {
			background-color: #fff;
			p {
				font-size: 24px;
				color: #333;
				margin: 0;
				font-weight: bold;
				line-height: 60px;
				text-align: center;
			}
		}
	}
	@media only screen and (max-width: 627px) {
		.el-header .logo {
			float: none;
    	width: auto;
     	text-align: center; 
		}
		.el-header .navBar {
			display: none;
		}
	}
	.el-main {
		padding: 0;
		.bg {
			&.bg-s {
				background: url(../../assets/images/bg1.jpg) no-repeat center center;
				background-size: 100%;
				height: 100px;
			}
			&.bg-b {
				background: url(../../assets/images/bg2.jpg) no-repeat fixed center center;
				height: 300px;
			}
		}
		.box {
			padding: 30px 20px 10px;
			border-bottom: 1px solid #eee;
			&#profile {
				.profile-img {
					width: 100px;
					height: 100px;
					border-radius: 50%;
					overflow: hidden;
					margin: 0 auto;
					border: 1px solid #eee;
					img {
						width: 100%;
						height: 100%;
					}
				}
				.profile-content {
					font-size: 16px;
					color: #333;
					p {
						text-align: center;
						line-height: 30px;
						padding: 0 40px;
					}
				}
				.skill-table {
					overflow: hidden;
					table {
						&last-child {
							margin-bottom: 0;
						}
						margin-bottom: 10px;
						width: 100%;
						border: 1px solid #eee;
						thead {
							tr {
								background-color: #eee;
							}
						}
						tbody {
							tr {
								&:nth-child(even){
									background-color: #eee;
								}
							}
						}
						tr {
							th {
								padding: 10px;
								text-align: center;
								border-left: 1px solid #eee;
								&:first-child {
									border-left: none;
								}
							}
							td {
						    line-height: 21px;
								width: 50%;
								padding: 10px;
								border-top: 1px solid #eee;
								border-left: 1px solid #eee;
								text-align: center;
								&:first-child {
									border-left: none;
								}
							}
						}
					}
				}
			}
			&#picture {
				.el-scrollbar {
					height: 484px;
				}
				.el-tab-pane {
					padding: 10px;
					padding-bottom: 0;
					.el-row {
				    margin-bottom: 10px;
				    &:last-child {
							margin-bottom: 0;
						}
				    .box-card {
				    	overflow: hidden;
				    	.box-image {
				    		height: 178px;
				    		overflow: hidden;
				    		text-align: center;
		    		    line-height: 178px;
				    		.image {
				    			max-height: 100%;
			    		    vertical-align: middle;
				    			cursor: pointer;
					    	}
				    	}
				    	.box-desc {
				    		overflow: hidden;
				    		white-space: nowrap;
				    		text-overflow: ellipsis;
				    		padding-top: 10px;
				    		text-align: center;
				    		span {
				    			font-size: 14px;
				    			color: #666;
				    		}
				    	}
				    }
				  }
				}
			}
			&#project {
				/*background: url(../../assets/images/bg2.jpg) no-repeat fixed;*/
				.el-carousel__item {
					display: flex;
					align-items: center;
    			justify-content: center;
    			background-color: rgba(255,255,255,.8);
			  }
			}
			&#remarks {
				p {
					margin: 0;
					text-align: center;
					line-height: 30px;
					padding: 0 40px;
				}
			}
		}
	}
	.el-footer {
		padding: 10px 20px;
		height: auto !important;
		p {
			text-align: center;
			margin: 0;
			font-size: 14px;
			color: #ccc;
		}
	}
	.el-dialog {
		iframe {
			width: 100%;
			height: 100%;
			border: none;
		}
		.dialog-footer {
			overflow: hidden;
			text-overflow: ellipsis;
			white-space: nowrap; 
			a {
				color: #666;
				&:hover {
					color: dodgerblue;
				}
			}
		}
	}
	.previewBox {
		position: fixed;
    z-index: 2000;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
		.previewImg-mask {
	    position: absolute;
	    z-index: 1;
	    background-color: rgba(0,0,0,.5);
	    top: 0;
	    right: 0;
	    bottom: 0;
	    left: 0;
	    -webkit-transition: opacity .3s;
	    transition: opacity .3s;
	  }
	  .previewImg-container {
	  	.preview-close {
  			position: absolute;
  			top: 0;
  			right: 0;
  			width: 45px;
  			height: 45px;
  			cursor: pointer;
  			z-index: 3;
				background: url(../../assets/images/close.png) no-repeat center center;
				background-size: 100% 100%;
	  	}
			.preview-img {
				display: flex;
	    	text-align: center;
				align-items: center;
	    	justify-content: center;
				height: 80vh;
				margin: 5vh 0;
				img {
					position: relative;
	  			z-index: 2;
					max-width: 100%;
				  max-height: 100%;
				}
			}
			.preview-text {
				display: flex;
	    	text-align: center;
	    	justify-content: center;
				height: 8vh;
				padding: 0 10px;
				margin-bottom: 2vh;
				span {
					position: relative;
	  			z-index: 2;
					font-size: 20px;
					margin: 0;
					color: #fff;
				}
			}
			.arrow {
				span {
					position: absolute;
	  			z-index: 3;
					width: 50px;
					height: 50px;
					line-height: 50px;
					text-align: center;
					background-color: rgba(0,0,0,.1);
					font-size: 20px;
					color: #ffffff;
					top: 50%;
					margin-top: -25px;
					cursor: pointer;
					font-family: "宋体";
					&.prev {
						left: 0;
					}
					&.next {
						right: 0;
					}
					&:hover {
						background-color: rgba(0,0,0,.5);
					}
				}
			}
		}
	}
	.fade-enter-active, .fade-leave-active {
	  transition: opacity .3s;
	}
	.fade-enter, .fade-leave-to {
	  opacity: 0;
	}
</style>
<style>
	.el-loading-spinner {
		position: fixed;
    top: 300px;
    margin-top: 0;
  }
</style>



